import { mapMutations, mapState } from 'vuex';
import { urlToJson } from '~/assets/js/utils/tools';
import { sha256 } from 'js-sha256';

export default {
  layout: 'global',
  name: 'AppLogin',
  meta: {
    isLoginPage: true
  },
  head: {
    title: '登录',
    titleTemplate: '%s - DJL箫氏的博客!',
    htmlAttrs: {
      lang: 'en',
      amp: true
    },
    meta: [
      {
        hid: 'description',
        name: 'description',
        content: '这是DJL箫氏的博客的登录页'
      }
    ]
  },
  components: {},
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, trigger: 'blur', message: '请输入用户名' }
        ],
        password: [{ required: true, trigger: 'blur', message: '请输入密码' }]
      }
    };
  },
  computed: {
    ...mapState([])
  },
  methods: {
    ...mapMutations({
      setUserMsg: 'user/setUserInfo'
    }),
    login() {
      const { response } = this.$http({
        url: '/login',
        data: {
          username: this.form.username,
          password: sha256(this.form.password)
        },
        method: 'post',
        showSuccessMsg: true,
        showErrorMsg: true
      });

      response.then(
        (user) => {
          this.setUserMsg({
            username: user.username,
            info: user.info,
            avatarKey: user.avatarKey,
            role: user.role,
            email: user.email,
            _id: user._id
          });
          this.goToNextPage();
        },
        () => {}
      );
    },
    formValidate() {
      if (this.$refs.form) {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.login();
          }
        });
      }
    },
    goToNextPage() {
      // 返回登录前的页面
      const urlParams = urlToJson();
      const nextPage =
        urlParams && urlParams._redirectUrl ? urlParams._redirectUrl : '/';
      this.$router.push(nextPage);
    },
    goToRegisterPage() {
      this.$router.push('/register');
    }
  }
};
